<template>
    <div class="grid_container">
        <div style="background-color: #8cc5ff" class="item item8">
            <div class="three_angles"></div>
        </div>

        <div v-for="n in 7" style="background-color: #8cc5ff" :class="'item '+'item'+n">{{n}}</div>
        <div class="item item9">
            <div class="clock">
                <div class="hour_line" :style="{transform:'rotate('+hourAngles+'deg)'}">
                    <div class="three_angles"></div>
                    <div v-for="n in 5 " class="line_part">
                    </div>

                </div>
                <div class="minute_line" :style="{transform:'rotate('+minuteAngles+'deg)'}">
                    <div class="three_angles"></div>
                    <div v-for="n in 7 " class="line_part">
                    </div>
                </div>
                <div class="second_line" :style="{transform:'rotate('+secondAngles+'deg)'}">
                    <div class="three_angles"></div>
                    <div v-for="n in 9" class="line_part">
                    </div>
                </div>
                <div class="clock_scale_1">
                    <div>9</div><div>3</div>
                </div>
                <div class="clock_scale_2">
                    <div>10</div><div>4</div>

                </div>
                <div class="clock_scale_3">
                    <div>11</div><div>5</div>
                </div>
                <div class="clock_scale_4">
                    <div>12</div><div>6</div>
                </div>
                <div class="clock_scale_5">
                    <div>1</div><div>7</div>
                </div>
                <div class="clock_scale_6">
                    <div>2</div><div>8</div>
                </div>
                <div class="clock_digital" v-show=showDigital>{{showHour}}:{{showMinute}}:{{showSecond}}</div>
            </div>

        </div>
    </div>
</template>

<script>
    import tools from "../../utils/tools"

    export default {
        name: "GridLayout",
        data() {
            return {
                arr: ['a', 'b'],
                seconds: 0,
                minutes: 0,
                hours: 0,
                showHour:0,
                showSecond:0 ,
                showMinute:0,
                showDigital:false,
            }
        },
        created() {
            let _this = this;
            this.showSecond = tools.date().getSeconds();
            _this.seconds = tools.date().getSeconds();
            _this.minutes = tools.date().getMinutes() + _this.seconds / 60;
            _this.hours = tools.date().getHours() + _this.minutes / 60;
            setInterval(function () {
                _this.showDigital=true;
                _this.showSecond = tools.date().getSeconds();
                // tools.setClosureData(_this.seconds);
                // console.log(tools.closureData)
                _this.showHour = tools.date().getHours();
                _this.showMinute = tools.date().getMinutes();
                _this.seconds += 1;
                _this.minutes += 1 / 60;
                _this.hours += 1 / 3600;
                // debugger
            }, 1000)
        },
        computed: {
            // closeData:{
            //     get(){
            //         return tools.closureData;//只能取到 初始值
            //     },
            //     set(val){
            //         // console.log("闭包函数参数closureData变为"+val)
            //     },
            // },
            secondAngles: {
                get() {//回调函数 当需要读取当前属性值是执行，根据相关数据计算并返回当前属性的值
                    return this.seconds / 60 * 360 + 90;
                },
                set(val) {//监视当前属性值的变化，当属性值发生变化时执行，更新相关的属性数据
                    this.showSeconds = val;
                    //val就是secondAngles的最新属性值
                }
            },
            minuteAngles: {
                get() {
                    return this.minutes / 60 * 360 + 90;
                },
                set(val) {
                }
            },
            hourAngles: {
                get() {
                    return this.hours / 12 * 360 + 90;
                },
                set(val) {
                }
            }
        },
        // watch:{
        //     closeData:function(val){
        //         console.log("闭包函数参数closureData变为"+val)
        //     }
        // }
    }
</script>

<style lang="scss" scoped>
    .grid_container {
        width: calc(100% - 20px);
        margin: 10px 10px;
        height: calc(100vh - 81px);
        display: grid; //设为网格布局以后，容器子元素（项目）的float、display: inline-block、display: table-cell、vertical-align和column-*等设置都将失效
        grid-template-columns: [col_1] 1fr [col_2] 1fr [col_3] 1fr [col_4] 1fr [col_5] minmax(100px, 1fr) [col_6]; //repeat(2, 100px 20px 80px);重复模式
        /*grid-template-columns: repeat(auto-fill, 100px);//单元格的大小是固定的，但是容器的大小不确定。容纳尽可能多的单元格，使用auto-fill关键字表示自动填充。*/
        grid-template-rows: [row_1] 1fr [row_2] 1fr [row_3] 1fr [row_4]; //grid-template-columns属性和grid-template-rows属性里面，还可以使用方括号，指定每一根网格线的名字
        /*row-gap: 20px; //行间距 column-gap: 20px;//列间距*/
        gap: 20px 20px;
        grid-auto-flow: row; //默认是row 排列顺序 先行后列 会出线空位置 row dense 布满位置
        justify-items: stretch; //start | end | center | stretch; 网格内元素位置 place-items属性是align-items属性和justify-items属性的合并简写形式。
        align-items: stretch; //start | end | center | stretch;
        grid-template-areas: 'a a b c' 'd e f c' '. i i .' //. 表示未命名区域
    }
    .three_angles{
        width: 0;
        border-top: 5px solid red;
        border-bottom: 5px solid rgba(225, 255, 201, 0);
        border-left: 5px solid rgba(225, 255, 201, 0);
        border-right: 5px solid rgba(225, 255, 201, 0);
        transform: rotate(90deg);
    }
    .item9 {
        grid-column-start: span 3; //左边框所在的垂直网格线 span 3跨越3个基本网格;
        grid-column-end: col_5; //右边框所在的垂直网格线  简写合并grid-column:  span 3/col5
        grid-row-start: row_1; //上边框所在的水平网格线
        grid-row-end: row_3; //下边框所在的水平网格线     grid-row:  / ;
        background-color: blue;
        min-height:210px ;
        /*justify-self: center;*/
        /*align-self: stretch;*/
        display: flex;
        justify-content: center;
        align-items: center;

        .clock {
            width: 210px;
            height: 210px;
            background-color: white;
            border-radius: 105px;

            .hour_line {
                transition: all 1s;
                width: 60px;
                height: 6px;
                position: relative;
                top: 100px;
                right: -45px;
                /*transform: rotate(180deg);*/
                transform-origin: right;
                display: flex;
                justify-content: space-between;
                align-items: center;
                opacity: .8;
                z-index: 100;
                .three_angles{
                    width: 0;
                    border-top: 5px solid red;
                    border-bottom: 5px solid rgba(225, 255, 201, 0);
                    border-left: 5px solid rgba(225, 255, 201, 0);
                    border-right: 5px solid rgba(225, 255, 201, 0);
                    transform: rotate(90deg);
                }
                .line_part {
                    width: 6px;
                    height: 100%;
                    background-color: red;
                }
                .line_part_arrow{
                    width: 6px;
                    height: 100%;
                    background-color: red;
                }
            }

            .minute_line {
                transition: all 1s;
                width: 80px;
                height: 4px;
                position: relative;
                top: 95px;
                right: -25px;
                /*transform: rotate(180deg);*/
                transform-origin: right;
                display: flex;
                justify-content: space-between;
                align-items: center;
                opacity: .8;
                z-index: 200;
                .three_angles{
                    width: 0;
                    border-top: 3px solid #2222ff;
                    border-bottom: 3px solid rgba(225, 255, 201, 0);
                    border-left: 3px solid rgba(225, 255, 201, 0);
                    border-right: 3px solid rgba(225, 255, 201, 0);
                    transform: rotate(90deg);
                }
                .line_part {
                    width: 4px;
                    height: 100%;
                    background-color: blue;
                }
            }

            .second_line {
                /*transition: all 1s;*/
                width: 100px;
                height: 2px;
                position: relative;
                top: 92px;
                left: 5px;
                transform-origin: right;
                display: flex;
                justify-content: space-between;
                align-items: center;
                opacity: .8;
                z-index: 300;
                .three_angles{
                    width: 0;
                    border-top: 1px solid #040418;
                    border-bottom: 1px solid rgba(225, 255, 201, 0);
                    border-left: 1px solid rgba(225, 255, 201, 0);
                    border-right: 1px solid rgba(225, 255, 201, 0);
                    transform: rotate(90deg);
                }
                .line_part {
                    width: 2px;
                    height: 100%;
                    background-color: black;
                }
            }

            .clock_scale_1 {
                width: 100%;
                height: 16px;
                position: relative;
                top: 85px;
                display: flex;
                justify-content: space-between;
                align-items: center;
            }

            .clock_scale_2 {
                width: 100%;
                height: 16px;
                position: relative;
                top: 69px;
                transform: rotate(30deg);
                display: flex;
                justify-content: space-between;
                align-items: center;
                div{
                    transform: rotate(-30deg);

                }
            }

            .clock_scale_3 {
                width: 100%;
                height: 16px;
                position: relative;
                top: 53px;
                transform: rotate(60deg);
                display: flex;
                justify-content: space-between;
                align-items: center;
                div{
                    transform: rotate(-60deg);

                }
            }

            .clock_scale_4 {
                width: 100%;
                height: 16px;
                position: relative;
                transform: rotate(90deg);
                top: 37px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                div{
                    transform: rotate(-90deg);

                }
            }

            .clock_scale_5 {
                width: 100%;
                height: 16px;
                position: relative;
                transform: rotate(120deg);
                top: 21px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                div{
                    transform: rotate(-120deg);

                }
            }

            .clock_scale_6 {
                width: 100%;
                height: 16px;
                position: relative;
                transform: rotate(150deg);
                top: 5px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                div{
                    transform: rotate(-150deg);

                }
            }
            .clock_digital{
                width: 60px;
                height: 20px;
                background-color: white;
                border: 1px solid #ecf0f9;
                border-radius:2px;
                position:relative;
                opacity: .7;
                left: 73px;
                top: -14px;
                z-index: 400;

            }


        }
    }

    .item2 {
        grid-area: i; //指向容器的grid-template-areas中的区域
        /*justify-self: end;//start | end | center | stretch; 设置项目中文本位置 stretch：拉伸默认值*/
        /*align-self: center;//start | end | center | stretch;*/
    }

</style>



